@import url(https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,100;1,300&family=Montserrat:wght@100&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,100;1,300&family=Montserrat:wght@100;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300&display=swap);


html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}


.header{
    background-color: rgb(233, 233, 246);
    margin-left:300px;
    display:flex;
    justify-content:center;
    align-items:center;
    height:70px;
    box-sizing: border-box;
    padding:5px 10px;
}

.header__icon{
    display:none;
    cursor:pointer;
}

@media only screen and (max-width: 960px) {
    .header{
        margin-left:0px;
        justify-content:space-between;
    }
    .header__icon{
        display:block;
    }
}


.header__search{
    
    height: 40px;
    display: flex;
    align-items: center;
    width: 50%;
    min-width: 250px;
    border-radius:50px;
}

.header__search-input{
    border: none;
    flex: 1 1;
    height: 100%;
    outline: none;
    padding: 2px 15px;
    font-size: 16px;
    box-sizing:border-box;
    border-top-left-radius: 50px;
    border-bottom-left-radius: 50px;

}

.header__search-input::-webkit-input-placeholder{
    font-size:15px;
    color:rgba(180, 180, 180, 0.472);
}

.header__search-input:-ms-input-placeholder{
    font-size:15px;
    color:rgba(180, 180, 180, 0.472);
}

.header__search-input::placeholder{
    font-size:15px;
    color:rgba(180, 180, 180, 0.472);
}

.header__search-btn{
    height: 100%;
    width: 40px;
    border: none;
    outline: none;
    background-color: white;
    border-left: 1px solid rgba(200, 200, 200, 0.383);
    cursor:pointer;
    border-top-right-radius: 50px;
    border-bottom-right-radius: 50px;
    display:flex;
    justify-content:center;
    align-items:center;
}
.select__name{
height:50px;
display:flex;
justify-content:space-between;
box-sizing:border-box;
padding:5px 10px;
align-items:center;
color:white;
font-weight:bold;
cursor:pointer;
}

.select__option{
    height:50px;
    display:flex;
    justify-content:space-between;
    box-sizing:border-box;
    padding:5px 10px;
    align-items:center;
    color:rgb(0, 0, 0);
    font-weight:bold;
    cursor:pointer;
    background-color: rgba(205, 228, 233, 0.452);
}

.select__option:hover{
background-color: rgb(147, 207, 219);
}
.sideBar{
    background-color: rgb(212, 212, 212);
    position:absolute;
    top:0px;
    left:50px;
    width:250px;
    height:100vh;

    display:flex;
    flex-direction: column;
    justify-content: flex-start;
   
    
}

@media only screen and (max-width: 960px) {
    .sideBar{
        display:none;
    }
}

.drawerSidebarContent{
    width:300px !important;
}


.priceBoxes{
   
    display:flex;
    justify-content: space-between;
}

.priceBox{
    background-color: white;
    box-sizing: border-box;
    padding: 2px 7px;
}

.drawerSidebarContent{
    display:flex !important;
    flex-direction: column !important;
}
.main-nav{
    background-color:aqua;
    position:absolute;
    top:0px;
    left:0px;
    display:flex;
    justify-content:flex-start;
    align-items:center;
    flex-direction: column;
    width:50px;
    height:100vh;
}

@media only screen and (max-width: 960px) {
    .main-nav{
        display:none;
    }
}
.catalog {
  background-color: rgba(240, 240, 240, 0.459);
  position: absolute;
  left: 300px;
  top: 70px;
  width: calc(100vw - 300px);
  height: calc(100vh - 70px);
  overflow-y: scroll;
  box-sizing: border-box;
  padding: 10px;
}

@media only screen and (max-width: 960px) {
  .catalog {
    width: 100%;
    left: 0px;
  }
}

.catalog__container {
  width: 100% !important;
  margin: 0px !important;
  height: calc(100vh - 70px);
}

.product__image {
  height: 270px;
  margin-bottom: 10px;
  max-width:100%;
}

.product__title {
  font-size: 16px;
  font-family: "Montserrat", sans-serif;
  font-weight: bold;
  text-align: left;
  width: 100%;
  text-overflow: ellipsis;
  text-transform: uppercase;
  line-height: 24px;
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}

.product__category {
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
  width: 100%;
  text-align: left;
  font-size: 15px;
  color:rgb(121, 121, 121);
}

.product__category-text {
  color: blue;
}

.product__price {
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
  width: 100%;
  text-align: left;
  font-size: 15px;
  padding: 15px 0px 10px 0px;
  border-top: 1px solid rgba(182, 182, 182, 0.411);
  color:rgb(121, 121, 121);
}

/* modal */

.modal{
  
  width:80vw;
  min-width:300px;
  height:500px;
}

.modal__left-side{
flex:1 1 300px;
display:flex;
flex-direction:column;
justify-content:flex-start;
align-items:center;
font-family: "Montserrat", sans-serif;
}

.modal__price{
height:50px;
margin:10px 0px;
color:gray;
font-weight:bold;
}

.modal___image{
  max-width:100%;
  max-height:300px;
  margin:10px 15px 10px 5px;
}

.modal__right-side{
flex:2 1 500px;
display:flex;
flex-direction:column;
justify-content:flex-start;
align-items:flex-end;
font-family: "Montserrat", sans-serif;
}

.modal__title{
  font-weight: bold;
  font-size: 20px;
  align-self: end;
  margin: 10px;
}

.modal__category{
  font-weight: 700;
  align-self: end;
  margin: 20px 10px;
  font-size: 15px;
  
}

.modal__description{
  margin: 10px;
  width: calc(100% - 20px);
  text-align: end;
  border-bottom: 1px solid gray;
  padding: 10px 0px;
  line-height: 20px;
  font-weight:300;
}


.modal__description-text {
margin:12px;
font-weight:300;
line-height:24px;
}

.modal__close-btn{
  position: absolute;
  right: 15px;
  top: 15px;
  cursor:pointer;
}

.modal__gif{
  width:30px;
}

.modal__gif-wrapper{
  width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}
